.home {
    background-color: var(--background-gradient);
    color: var(--text-color);
    text-align: center;
    padding: 40px 20px;
}

.intro {
    padding: 60px 20px;
    background-color: var(--highlight-gradient);
    max-width: 800px;
    margin: 0 auto;
}

.intro h1 {
    font-size: 3.8rem;
    background: var(--highlight-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 20px;
}

.intro p {
    font-size: 1.2rem;
    line-height: 1.6;
    margin: 20px 0;
    color: var(--text-color);
}

.intro a {
    color: var(--highlight-gradient);
    text-decoration: none;
    font-weight: bold;
}

.intro a:hover {
    text-decoration: underline;
    color: var(--highlight-gradient);
}

.social-icons {
    margin-top: 20px;
}

.social-icons a {
    font-size: 1.5rem;
    margin: 0 15px;
    color: var(--highlight-gradient);
    text-decoration: none;
    transition: transform 0.2s ease;
}

.social-icons a:hover {
    transform: scale(1.2);
    color: var(--highlight-gradient);
}

.projects-preview {
    padding: 60px 20px;
    background-color: var(--background-gradient);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}



.projects-preview h2 {
    font-size: 2.5rem;
    background: var(--highlight-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: center;
    margin-bottom: 40px;
}

.mini-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    max-width: 1000px; /* Adjust the max-width as needed */
    width: 100%;
    padding: 0 20px;
    justify-items: center;
}

.mini-card {
    background-color: var(--background-gradient);
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(155, 89, 182, 0.3); /* Purple shadow */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: left;
}

.mini-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 30px rgba(128, 90, 213, 0.4); /* Brighter hover effect */
}

.mini-card .year {
    font-size: 0.9rem;
    color: var(--highlight-gradient);
    margin-bottom: 10px;
}

.mini-card h3 {
    font-size: 1.2rem;
    background: var(--highlight-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 10px;
}

.mini-card p {
    font-size: 1rem;
    color: var(--text-color);
    line-height: 1.6;
}

.project-links {
    display: flex;
    gap: 10px;
}

.link-button {
    padding: 8px 16px;
    background: var(--highlight-gradient);
    color: #ffffff;
    border: none;
    border-radius: 8px;
    font-weight: bold;
    text-decoration: none;
    transition: background 0.3s ease, transform 0.2s ease;
}

.link-button:hover {
    transform: scale(1.05);
}
