:root {
  /* Updated Light Theme Colors */
  --background-gradient: linear-gradient(180deg, #f8f5ff, #ebe2ff, #d6cfff);
  --highlight-gradient: linear-gradient(135deg, #bb86fc, #3700b3);
  --highlight-color: #9840fb;
  --text-color: #4b3c72; /* Deep purple for text */
  --link-color: #6d28f1; /* Vibrant purple for links */
  --card-background-color:linear-gradient(145deg, #ffffff, #f0f0ff);
  --navbar-background: var(--background-gradient); /* Use the gradient for navbar */
}

[data-theme="dark"] {
  /* Dark Theme Colors */
  --background-color: #121212;
  --text-color: #e0e0e0;
  --link-color: #9840fb;;
  --card-background-color: #1e1e1e;
  --highlight-color: #b06dfa;
  --highlight-gradient: linear-gradient(135deg, #bb86fc, #3700b3);
  --navbar-background: #121212;
}

body, html {
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

a {
  color: var(--link-color);
  text-decoration: none;
}

.project-card, .about-card, .mini-card {
  background-color: var(--card-background-color);
}

a:hover {
  color: #d3d3d3;
}
